import React from "react";
import "./App.css";
import Async from "react-async";
import Modal from "react-modal";

// taken from somewhere else
function debounce(func, wait, immediate) {
	var timeout;
	return function() {
		var context = this, args = arguments;
		var later = function() {
			timeout = null;
			if (!immediate) func.apply(context, args);
		};
		var callNow = immediate && !timeout;
		clearTimeout(timeout);
		timeout = setTimeout(later, wait);
		if (callNow) func.apply(context, args);
	};
};

// may as well hard code this
const validImageNames = [...Array(106).keys()].map( (num) => num + 1 ) // 1...106

const loadImage = (endpoint) => (name) => () =>
  fetch(`https://renyi-demo-backend.herokuapp.com/${endpoint}?name=${name}`)
    .then(res => (res.ok ? res : Promise.reject(res)))
    .then(res => res.json());

function RenyiImage(props) {
  return <Async promiseFn={loadImage(props.endpoint)(props.name)}>
    {({ data, error, isLoading }) => {
      // if (isLoading) return "Loading..."; ends up just being really ugly
      if (error) return `Something went wrong: ${error.message}`;
      if (isLoading) return props.onLoading;
      if (data) {
        var base64 = `data:image/jpeg;base64,${data["image"]}`;
        return (
          <div>
            <img className={`img_named_${props.name}`} src={base64} alt="Image of a building's facade"/>
          </div>
        );
  }
  return null;
        }}
</Async>
}

function RenyiImageNoLoadIndicator(props) { // definitely a hack, but otherwise renyi images were getting redrawn for no reason
  return <Async promiseFn={loadImage(props.endpoint)(props.name)}>
    {({ data, error, _ }) => {
      // if (isLoading) return "Loading..."; ends up just being really ugly
      if (error) return `Something went wrong: ${error.message}`;
      if (data) {
        var base64 = `data:image/jpeg;base64,${data["image"]}`;
        return (
          <div>
            <img className={`img_named_${props.name}`} src={base64} alt="Image of a building's facade"/>
          </div>
        );
  }
  return null;
        }}
  </Async>
}

function Facade(props) { return <RenyiImageNoLoadIndicator endpoint="get_facade" name={props.name}/> }
function Fake(props) { return <RenyiImage onLoading="Loading..." endpoint="get_fake" name={props.name}/> }
function Real(props) { return <RenyiImage onLoading="Loading..." endpoint="get_real" name={props.name}/> }
function FacadeColumn(props) {
  const images = props.names.map(
    (name) => <Facade className="facade-in-a-column" name={name} key={name}/>
  )
  return <div className="column">
    {images}
  </div>
}
function FacadeGrid(props) {
  const [width, setWidth] = React.useState(window.innerWidth)

  React.useEffect(() => { 
      const debouncedResizeHandler = debounce(() => {
      setWidth(window.innerWidth)
    }, 250, false);

    window.addEventListener('resize', debouncedResizeHandler)
  })

  const count = Math.floor(props.ratio * width / 256.0)
  const columns = [...Array(count).keys()].map(
    (i) => validImageNames.filter((num) => num % count == ((i + 1) % count))
  )

  return (
    <div className="row">
      {columns.map((column) => <FacadeColumn className="facade-column" names={column} key={column[0]}/>)}
    </div>
  )
}

function RenyiModal(props) {
  return <Modal
    isOpen={props.isOpen}
    onRequestClose={props.onRequestClose}
    className="modal"
  >
    <div className="row">
      <div className="column">
        <p className="label">Fake:</p>
        <Fake name={props.name}/>
      </div>

      <div className="column">
      <p className="label">Real:</p>
        <Real name={props.name}/>
      </div>
    </div>
  </Modal>
}

function App() {
  const [modalIsOpen, setIsOpen] = React.useState(false)
  const [imageName, setName] = React.useState("")
  const openModal = () => setIsOpen(true)
  const closeModal = () => setIsOpen(false)

  const handleClick = (e) => {
    const targetName = e.target.className
    if (targetName.includes("img_named_")) {
      const image_num = targetName.substring(10) // trims img_named_ part
      setName(image_num)
      openModal()
    }
  }

  return (
    <div className="App" onClick={handleClick}>
      <head className="App-header">
      <h1 className="title">Rnyi Demo</h1>
      <p className="title">Click on a facade to see the real and generated buildings to which it corresponds. The generated images were created using a version of the Pix2Pix GAN with the Rnyi entropy loss, trained with alpha = 3.</p>
      <FacadeGrid ratio={0.8}/> {/* ratio here refers to how much of the screen's width can be image */}
      </head>
      <RenyiModal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        name={imageName}
      />
    </div>
  );
}

export default App;
